.viewSource{margin-top:12px;display:block;cursor:pointer;padding-left:13px;background:white url('/Reference/Resources/Images/CollapsiblePanel/expand.gif') no-repeat left center;
}
.viewSource.collapse{background:white url('/Reference/Resources/Images/CollapsiblePanel/collapse.gif') no-repeat left center;
}
.operationList{width:100%;
}
.operationList td{width:25%;vertical-align:top;
}
.imgLabel{padding-bottom:20px;
}
.controlsSamplesContainer{list-style-type:none;display:block;background-color:#F9F9F9;border-top:solid 1px #DFE3E2;padding:2px 7px 0 7px;margin-top:20px;
}
.controlsSamplesContainer ul{line-height:1.8;width:200px;padding:0;margin:0 0 .4em;list-style:none outside none;float:left;
}
.controlsSamplesContainer li{padding:0;display:block;float:left;width:130px;
}
.controlsSamplesContainer a{color:#999;
}
.controlsSamplesContainer li.group{font-weight:bold;color:#666;
}
.control{font-weight:bold;
}
.tblProperties{margin:0;padding:0;width:100%;border-collapse:collapse;
}
.tblProperties thead tr{background-color:#eeeff1;
}
.tblProperties thead td{font-weight:bold;padding:3px;
}
.tblProperties thead td.hName{width:25%;
}
.tblProperties thead td.hType{width:10%;
}
.tblProperties thead td.hDefault{width:15%;
}
.tblProperties thead td.hDescription{width:50%;
}
.tblProperties tbody td{line-height:1.8;padding:3px;border-bottom:solid 1px #eeeff1;
}
.tblProperties tbody td.code{font-family:Monospace;
}
h4.example,h4.property{margin-top:12px;margin-bottom:5px;font-size:11px;
}
h4.property{color:#666;
}
.toolbarWhite{padding-left:5px;background-color:#FFF;border:solid 1px #FFF;border-bottom:none;display:block;
}
.sampleBox .textArea.large{width:786px;
}
table.customers-grid th.is-valid{width:2%;
}
table.customers-grid th.name{width:33%;
}
table.customers-grid th.language{width:50%;
}
table.customers-grid th.action,table.customers-grid td.action{text-align:right!important;width:15%;
}
table.customers-search-grid th.is-valid{width:2%;
}
table.customers-search-grid th.name{width:33%;
}
table.customers-search-grid th.language{width:65%;
}
